import Router from 'vue-router'
import ThirdParty from '../components/third_party/third_party.vue'
import EBank from '../components/e_bank/e_bank.vue'
/* eslint-disable*/
Vue.use(Router)

export default new Router({
  routes: [{
    path: '',
    redirect: '/thirdParty'
  }, {
    path: '/thirdParty',
    name: 'thirdParty',
    component: ThirdParty
  }, {
    path: '/eBank',
    name: 'eBank',
    component: EBank
  }]
})





