import './pay.less'

import Msg from 'vux/src/components/msg/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'

import router from './router'

/* eslint-disable*/


new Vue({
el: '#body',
data: {
icon: 'success',
itemIndex: 0
},
created () {
this.$router.push({path:'thirdParty'})
},
methods: {
showPayment(){
if (this.itemIndex === 0) {

this.$router.push({path:'thirdParty'})
} else if (this.itemIndex === 1) {

this.$router.push({path:'eBank'})
}
}
},
router,
components: {
Msg,
ButtonTab,
ButtonTabItem
}
})



